<template>
  <div class="container">
    <ul class="folder">
    <div class="folder__name">{{name}}</div>
      <li class="folder__file">
        <template v-for="folder in folders">
          <FolderView :name="folder.name" :folders="folder.folders" :files="folder.files"/>
        </template>
        <template v-for="file in files">
          <FileView :name="file.name"/>
        </template>
      </li>
  </ul>
  </div>
</template>

<script>
import FileView from "./FileView";
export default {
  name: 'FolderView',
  components: {FileView},
  props: {
    name: String,
    folders:Array,
    files:Array
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap');
.container {
  width: 500px;
  margin: auto;
}
.folder{
  margin: 15px auto 15px auto;
  padding: 0;
  color: #fff;
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  list-style-type: none;
  &__name {
    width: 130px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #3988FF;
    border-radius: 5px;
    border: none;
    text-transform: uppercase;
    cursor: pointer;
    &:hover {
      background-color: darken(#3988FF, 30%);
      transition: 0.3s all;
    }
  }
  &__file {
    padding-left: 40px;
    border-left: 1px solid #3988FF;
  }
}
</style>
